import React, { useState, useEffect } from 'react';
import { db } from './firebase';
import { ref, get, set, onValue } from 'firebase/database';
import ReactConfetti from 'react-confetti';
import './App.css';

function App() {
  const [response, setResponse] = useState('');
  const [currentMeme, setCurrentMeme] = useState('');
  const [clickCount, setClickCount] = useState(0);
  const [displayCount, setDisplayCount] = useState(0);
  const [showConfetti, setShowConfetti] = useState(false);
  const [currentMarqueeIndex, setCurrentMarqueeIndex] = useState(0);
  const [imageError, setImageError] = useState(false);
  const [isMuted, setIsMuted] = useState(false);
  const [audio] = useState(new Audio('/sounds/click.mp3'));

  useEffect(() => {
    // Set initial volume
    audio.volume = 0.4;
  }, [audio]);

  const playSound = () => {
    if (!isMuted) {
      // Reset the sound to start
      audio.currentTime = 0;
      // Play the sound
      const playPromise = audio.play();
      
      if (playPromise !== undefined) {
        playPromise.catch(error => {
          console.log("Audio play failed:", error);
        });
      }
    }
  };

  const marqueeTexts = [
    "$SKILL IS UP... BUT YOU'RE STILL DOWN",
    "DIAMOND HANDS? MORE LIKE GLASS HANDS",
    "$SKILL IS MOONING... BUT NOT FOR YOU",
    "NGMI? YEAH, THAT'S YOU",
    "BOUGHT HIGH SOLD LOW? CLASSIC",
    "YOUR PORTFOLIO IS A SKILL ISSUE"
  ];

  // Rotate marquee text every 5 seconds
  useEffect(() => {
    const interval = setInterval(() => {
      setCurrentMarqueeIndex((prev) => (prev + 1) % marqueeTexts.length);
    }, 5000);
    return () => clearInterval(interval);
  }, []);

  useEffect(() => {
    const countRef = ref(db, 'buttonClicks');
    const unsubscribe = onValue(countRef, (snapshot) => {
      const count = snapshot.val() || 0;
      setClickCount(count);
    });
    return () => unsubscribe();
  }, []);

  // Animate counter
  useEffect(() => {
    if (displayCount !== clickCount) {
      const diff = clickCount - displayCount;
      const step = Math.max(1, Math.floor(Math.abs(diff) / 10));
      const timeout = setTimeout(() => {
        setDisplayCount(prev => 
          diff > 0 ? Math.min(clickCount, prev + step) : Math.max(clickCount, prev - step)
        );
      }, 50);
      return () => clearTimeout(timeout);
    }
  }, [displayCount, clickCount]);

  // Check for milestones
  useEffect(() => {
    if (clickCount > 0 && clickCount % 100 === 0) {
      setShowConfetti(true);
      setTimeout(() => setShowConfetti(false), 5000);
    }
  }, [clickCount]);

  const responses = [
    "Skill issue detected. ",
    "Bro, have you considered being better? ",
    "You dropped this ",
    "404: Skill not found. ",
    "Financial freedom is NOT in your future. ",
    "Not even ChatGPT can save you. ",
    "You're the problem. Not the market. ",
    "This ain't even cope anymore, bro. ",
    "Touch grass. Immediately. ",
    "Bro thinks he's him. Spoiler: He's not. ",
    "Do you even lift your financial goals? ",
    "This is why your mom calls you special. ",
    "Average NPC behavior detected. ",
    "Bro is speedrunning poverty. ",
    "L + Ratio + Broke + No bitches. ",
    "You're not a player, you're an NPC, bro. ",
    "Skill diff. Massive skill diff. ",
    "Bro's portfolio built different in the worst way. ",
    "Imagine clicking this button thinking you'd win. ",
    "You remind me of the guy who sold Bitcoin at $1. ",
    "Bro skipped the financial literacy tutorial. ",
    "Your financial advice? Straight from Wish. ",
    "You thought you did something there, huh? ",
    "Your wallet crying rn. ",
    "This button has more ambition than you. ",
    "You're not gonna make it. NGMI certified. ",
    "You spent 20 minutes clicking this. Go outside. "
  ];

  const memes = [
    "https://media.tenor.com/v_JxS1NvoOYAAAPo/squirel.mp4",
    "https://media.tenor.com/hZEdisC4gbMAAAPo/the-terrifier-costume.mp4",
    "https://media.tenor.com/dl7oT9GywQgAAAPo/noneckjay-meme.mp4",
    "https://media.tenor.com/vS3naLKhwW0AAAPo/funny-animals-peakaboo.mp4",
    "https://media.tenor.com/-gs-3Tz5YVIAAAPo/mr-peabody-hypnotize.mp4",
    "https://media.tenor.com/nx8sLFHcRksAAAPo/voyagr-borzoi.mp4",
    "https://media.tenor.com/rOZ7Di_8WJAAAAPo/crip-shrimp.mp4",
    "https://media.tenor.com/Wfl8-mmYg34AAAPo/blake-door.mp4",
    "https://media.tenor.com/Wm0jcjNtY5QAAAPo/spongebob-wtf.mp4",
    "https://media.tenor.com/3fp1G9hkLiIAAAPo/monkey.mp4"
  ];

  // Fallback GIF
  const fallbackGif = "https://media.giphy.com/media/v1.Y2lkPTc5MGI3NjExZmU2ZDRjZjFjYjBlYzYwYjIwMzU4Nzc4NDJlNDMyMzFiZDk3ODcwNyZjdD1n/ttolwR4oX5c5rGFO6y/giphy.gif";

  const generateResponse = async () => {
    playSound();
    // Increment counter in Firebase
    const newCount = clickCount + 1;
    await set(ref(db, 'buttonClicks'), newCount);

    const randomResponse = responses[Math.floor(Math.random() * responses.length)];
    const randomMeme = memes[Math.floor(Math.random() * memes.length)];
    setResponse(randomResponse);
    setCurrentMeme(randomMeme);
  };

  return (
    <div className="App">
      <button 
        className="sound-toggle" 
        onClick={() => setIsMuted(!isMuted)}
        aria-label={isMuted ? "Unmute sounds" : "Mute sounds"}
      >
        {isMuted ? (
          <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" fill="currentColor">
            <path d="M13 7.5v9c0 .28-.22.5-.5.5s-.5-.22-.5-.5v-9c0-.28.22-.5.5-.5s.5.22.5.5zm4 0v9c0 .28-.22.5-.5.5s-.5-.22-.5-.5v-9c0-.28.22-.5.5-.5s.5.22.5.5zm-8 0v9c0 .28-.22.5-.5.5s-.5-.22-.5-.5v-9c0-.28.22-.5.5-.5s.5.22.5.5z"/>
          </svg>
        ) : (
          <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" fill="currentColor">
            <path d="M14.016 3.234q3.047 0.656 5.016 3.117t1.969 5.648-1.969 5.648-5.016 3.117v-2.063q2.203-0.656 3.586-2.484t1.383-4.219-1.383-4.219-3.586-2.484v-2.063zM16.5 12q0 2.813-2.484 4.031v-8.063q2.484 1.219 2.484 4.031zM3 9h3.984l5.016-5.016v16.031l-5.016-5.016h-3.984v-6z"/>
          </svg>
        )}
      </button>

      {showConfetti && (
        <ReactConfetti
          width={window.innerWidth}
          height={window.innerHeight}
          colors={['#00ff00', '#ffffff', '#003300']}
          recycle={false}
        />
      )}

      <div className="counter-container">
        <span className="counter-text">
          <span className="counter-number">{displayCount.toLocaleString()}</span>
          <span className="counter-label"> Skill Issues Detected</span>
        </span>
      </div>

      <h1>BROKE? SKILL ISSUE.</h1>
      <div className="subtext">Not financial advice. Definitely your fault tho.</div>
      
      <button className="try-again-btn" onClick={generateResponse}>
        Try Again
      </button>

      <div className="social-icons">
        <a 
          href="https://x.com/SkillIssueSolx" 
          target="_blank" 
          rel="noopener noreferrer"
          className="social-icon" 
          aria-label="Twitter"
        >
          <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" fill="currentColor">
            <path d="M18.244 2.25h3.308l-7.227 8.26 8.502 11.24H16.17l-5.214-6.817L4.99 21.75H1.68l7.73-8.835L1.254 2.25H8.08l4.713 6.231zm-1.161 17.52h1.833L7.084 4.126H5.117z"/>
          </svg>
        </a>
        <a 
          href="https://t.me/SkillIssueSolx" 
          target="_blank" 
          rel="noopener noreferrer"
          className="social-icon" 
          aria-label="Telegram"
        >
          <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" fill="currentColor">
            <path d="M11.944 0A12 12 0 0 0 0 12a12 12 0 0 0 12 12 12 12 0 0 0 12-12A12 12 0 0 0 12 0a12 12 0 0 0-.056 0zm4.962 7.224c.1-.002.321.023.465.14a.506.506 0 0 1 .171.325c.016.093.036.306.02.472-.18 1.898-.962 6.502-1.36 8.627-.168.9-.499 1.201-.82 1.23-.696.065-1.225-.46-1.9-.902-1.056-.693-1.653-1.124-2.678-1.8-1.185-.78-.417-1.21.258-1.91.177-.184 3.247-2.977 3.307-3.23.007-.032.014-.15-.056-.212s-.174-.041-.249-.024c-.106.024-1.793 1.14-5.061 3.345-.48.33-.913.49-1.302.48-.428-.008-1.252-.241-1.865-.44-.752-.245-1.349-.374-1.297-.789.027-.216.325-.437.893-.663 3.498-1.524 5.83-2.529 6.998-3.014 3.332-1.386 4.025-1.627 4.476-1.635z"/>
          </svg>
        </a>
      </div>

      <div className="response-container">
        {response && (
          <div className="meme-response">
            <h2>{response}</h2>
            {currentMeme && (
              <div className="meme-container">
                <img 
                  src={imageError ? fallbackGif : currentMeme} 
                  alt="reaction meme" 
                  className="meme-image"
                  onError={() => setImageError(true)}
                  onLoad={() => setImageError(false)}
                />
              </div>
            )}
          </div>
        )}
      </div>

      <div className="marquee-container">
        <div className="marquee">
          <span>{marqueeTexts[currentMarqueeIndex]} &nbsp;&nbsp;&nbsp;</span>
          <span>{marqueeTexts[currentMarqueeIndex]} &nbsp;&nbsp;&nbsp;</span>
          <span>{marqueeTexts[currentMarqueeIndex]} &nbsp;&nbsp;&nbsp;</span>
          <span>{marqueeTexts[currentMarqueeIndex]} &nbsp;&nbsp;&nbsp;</span>
        </div>
      </div>
    </div>
  );
}

export default App;